import { DeleteOutlined, EditOutlined } from "@ant-design/icons";
import { Button, Input, PageHeader, Table, Tag } from "antd";
import React from "react";
import { useEffect } from "react";
import { useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { deleteUserAction, layDanhSachUserAction } from "../../../redux/actions/QuanLyNguoiDungAction";

const onChange = (pagination, filters, sorter, extra) => {
    console.log("params", pagination, filters, sorter, extra);
};

const { Search } = Input;
export default function Users(props) {
    const { history } = props;
    console.log('history: ', history);
    const dispatch = useDispatch();
    ////////////////////////////////////////////////////////////////////////////////////////////////todo:
    //! ly d liu v
    const { userList } = useSelector((state) => state.QuanLyNguoiDungReducer);
    useEffect(() => {
        dispatch(layDanhSachUserAction());
    }, []);
    // ! debounce search
    const searchRef = useRef(null);
    // setData(userList);
    // ! dng useEffect  gi d liu v lu v slice
    useEffect(() => {
        // dispatch(NguoiDungSliceActions.getUserList());
    }, []);
    // let data = userList;
    // ! chnh li mng data
    // ! thm button
    const handleEditUser = (editAccount) => {
        console.log('editAccount: ', editAccount);
        history.push(`/admin/users/edit/${editAccount}`)
        // navigate(`edit/${userEdit.taiKhoan}`);
    };
    const handleDeleteUser = (deleteAccount) => {
        // console.log('deleteAccount: ', deleteAccount);
        dispatch(deleteUserAction(deleteAccount))
        // dispatch(NguoiDungSliceActions.deleteUser(userAccount));
    };

    const handleOnBack = () => {
        history.push("/admin")
    }
    // ! chnh li mng columns
    const columns = [
        {
            title: "H v tn",
            dataIndex: "hoTen",
            sorter: (a, b) => a.hoTen.length - b.hoTen.length,
            sortDirections: ["descend"],
            // width: "1%",
        },
        {
            title: "Ti khon",
            dataIndex: "taiKhoan",
            defaultSortOrder: "descend",
            sorter: (a, b) => a.taiKhoan.length - b.taiKhoan.length,
            // width: "1%",
        },
        {
            title: "Mt khu",
            dataIndex: "matKhau",
            defaultSortOrder: "descend",
            // width: "20%",
        },
        {
            title: "M User",
            dataIndex: "maLoaiNguoiDung",
            defaultSortOrder: "descend",
            render: (text, record, index) => {
                return text == "QuanTri" ? (
                    <Tag color="magenta">Qun tr</Tag>
                ) : (
                    <Tag color="cyan">Khch hng</Tag>
                );
            },
            // width: "20%",
        },
        {
            title: "Ty chnh",
            defaultSortOrder: "descend",
            render: (text, record, index) => {
                return (
                    <div className="flex gap-4">
                        <Button
                            onClick={() => {
                                handleEditUser(record.taiKhoan);
                            }}
                            type="primary"
                            shape="circle"
                            icon={<EditOutlined />}
                        />
                        <Button
                            onClick={() => {
                                handleDeleteUser(text.taiKhoan);
                            }}
                            type="default"
                            shape="circle"
                            icon={<DeleteOutlined />}
                        />
                    </div>
                );
            },
            // width: "20%",
        },
    ];
    const data = userList;
    // ! filter data
    const handleChange = (event) => {
        let { value } = event.target;
        value = value.toLowerCase();
        // ! ly k t cn tm==> gi API ==> khi tr v th ly data gn li state

        if (searchRef.current) {
            clearTimeout(searchRef.current);
        }
        searchRef.current = setTimeout(() => {
            // dispatch(NguoiDungSliceActions.searchUser(value));
            dispatch(layDanhSachUserAction(value));
        }, 300);

    };
    const onSearch = (value) => {
        dispatch(layDanhSachUserAction(value));
    };
    return (
        <div className=" mx-auto bg-slate-300">
            <PageHeader
                className="site-page-header"
                onBack={handleOnBack}
                title="User Management"
            />
            <div className="container">
                <div className="flex justify-between p-4">
                    <div className="col-6">
                        <Button
                            type="primary"
                            onClick={() => {
                                history.push("/admin/users/addnew")
                            }}
                        >
                            New User
                        </Button>
                    </div>
                    <div className="col-6 text-3xl text-black">
                        {/* //! lm chc nng search user */}
                        <Search
                            placeholder="input search text"
                            onChange={handleChange}
                            onSearch={onSearch}
                            enterButton
                        />
                    </div>
                </div>
            </div>

            <Table
                scroll={{
                    x: 1300,
                    y: 500,
                }}
                rowKey={(record) => record.email}
                columns={columns}
                dataSource={data}
                onChange={onChange}
            />
        </div>
    );
}
