import { Fragment, useEffect } from "react";
import { Route, Redirect } from "react-router";
import { USER_LOGIN } from "../../util/settings/config";
import Footer from "../HomeTemplate/Layout/Footer/Footer";
import Header from "../HomeTemplate/Layout/Header/Header";

const CheckoutTemplate = (props) => {
  //path, exact, Component

  const { Component, ...restProps } = props;
  // ! logic: khi va vo trang phi cho scroll to top
  useEffect(() => {
    window.scrollTo(0, 0);
  });

  // ! x l: Nu cha Login ( LocalStorage cha c token ) th mi i thng v trang login nghen.
  if (!localStorage.getItem(USER_LOGIN)) {
    return <Redirect to="/login" />;
  }

  return (
    <Route
      {...restProps}
      render={(propsRoute) => {
        //props.location,props.history,props.match

        return (
          <Fragment>
            {/* <Header /> */}
            <Component {...propsRoute} />
          </Fragment>
        );
      }}
    />
  );
};
export default CheckoutTemplate;
