import { Form, Input } from "antd";
import React from "react";
import { useDispatch } from "react-redux";
import { NavLink } from "react-router-dom";
import { NavHashLink } from "react-router-hash-link";
import { dangKyAction } from "../../redux/actions/QuanLyNguoiDungAction";

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 9,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 48,
    },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};
const RegisterForm = () => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const onFinish = (values) => {
    const registerData = { ...values };
    dispatch(dangKyAction(registerData));
  };
  return (
    <div className="pt-2 px-10 flex-col align-middle items-center ">
      <h2 className="text-center tracking-wide uppercase text-4xl text-red-700 font-bold lg:text-center xl:text-4xl mt-5 mx-auto">
        ng K
      </h2>
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={onFinish}
        size="large"
        layout="horizontal"
        scrollToFirstError
      >
        <Form.Item
          name="taiKhoan"
          label={
            <label className="text-white font-semibold italic">Ti Khon</label>
          }
          rules={[
            {
              required: true,
              message: "Vui lng nhp ti khon!",
              whitespace: false,
            },
            {
              min: 4,
              max: 10,
              message: "4 - 10 ch s",
            },
            {
              pattern: new RegExp(/^[a-z][a-z0-9_.]*$/),
              message: "Ti khon khng chp nhn k t c bit v ch in hoa",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          className="text-sm font-bold text-gray-700"
          name="hoTen"
          label={
            <label className="text-white font-semibold italic">H Tn</label>
          }
          rules={[
            {
              required: true,
              message: "Vui lng nhp h tn!",
              whitespace: true,
            },
            {
              pattern: new RegExp(/^([^0-9]*)$/),
              message: "Tn khng c cha s",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          className="text-sm font-bold text-gray-700"
          name="email"
          label={
            <label className="text-white font-semibold italic">Email</label>
          }
          rules={[
            {
              type: "email",
              message: "Email khng hp l!",
            },
            {
              required: true,
              message: "Vui lng in Email!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          className="text-sm font-bold text-gray-700 p-5"
          name="matKhau"
          label={
            <label className="text-white font-semibold italic">Mt Khu</label>
          }
          rules={[
            {
              required: true,
              message: "Vui lng nhp mt khu",
            },
            {
              pattern: new RegExp(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/),
              message:
                "t nht 8 k t, c t nht 1 ch v 1 s v khng c k t c bit",
            },
          ]}
          hasFeedback
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          className="text-sm font-bold text-gray-700"
          name="confirm"
          label={
            <label className="text-white font-semibold italic">Xc Nhn</label>
          }
          dependencies={["matKhau"]}
          hasFeedback
          rules={[
            {
              required: true,
              message: "Xc nhn li mt khu!",
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue("matKhau") === value) {
                  return Promise.resolve();
                }

                return Promise.reject(
                  new Error("Mt khu khng khp, vui lng kim tra li!")
                );
              },
            }),
          ]}
        >
          <Input.Password placeholder="Xc nhn li mt khu!" />
        </Form.Item>
        <Form.Item
          className="text-sm font-bold text-gray-700 "
          name="soDt"
          label={
            <label className="text-white font-semibold italic">
              S in Thoi
            </label>
          }
          rules={[
            {
              required: true,
              message: "Vui lng in s in thoi",
            },
            {
              min: 8,
              message: "t nht 8 ch s",
            },
          ]}
        >
          <Input
            style={{
              width: "100%",
            }}
          />
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <button
            type="danger"
            htmlType="submit"
            className=" rounded-lg border bg-red-500 hover:bg-red-700 py-2 w-full uppercase tracking-wider text-white text-lg font-bold"
          >
            ng k
          </button>
          <div className="my-3 flex align-middle items-center w-full">
            <p className="text-lg italic font-bold  text-gray-300">
               c ti khon ?
            </p>
            <NavHashLink smooth to="/login#dangNhap">
              <p className="underline text-lg ml-1 text-red-700 hover:text-white mt-2">
                ng Nhp
              </p>
            </NavHashLink>
          </div>
        </Form.Item>
      </Form>
    </div>
  );
};

export default RegisterForm;
