import React from "react";
import { Form, Input } from "antd";
import { useDispatch } from "react-redux";
import { editUserAction } from "../../redux/actions/QuanLyNguoiDungAction";
import { useEffect } from "react";
import { NavHashLink } from "react-router-hash-link";

export default function UserInfo({ data }) {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  useEffect(() => {
    form.setFieldsValue({
      taiKhoan: data.taiKhoan,
      matKhau: data.matKhau,
      hoTen: data.hoTen,
      email: data.email,
      soDT: data.soDT,
      maLoaiNguoiDung: data.maLoaiNguoiDung,
    });
  }, [form, data]);
  const onFinish = (values) => {
    const updateData = { ...values, maNhom: "GP00" };
    dispatch(editUserAction(updateData));
  };
  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  return (
    <div className="container border-2 border-black ">
      <div
        className="relative mt-28 w-4/5 bg-black py-5 px-24  mx-auto rounded-xl "
        style={{
          backgroundColor: " rgba(60, 59, 59, 0.4)",
        }}
      >
        <NavHashLink smooth to="/profile#userProfile">
          <p className="font-bold text-red-700 text-center text-4xl uppercase underline hover:text-red-600">
            Thng tin ti khon
          </p>
        </NavHashLink>
        <Form
          form={form}
          size="large"
          className="grid grid-cols-2"
          name="basic"
          labelCol={{
            span: 8,
          }}
          wrapperCol={{
            span: 16,
          }}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            label={
              <span className="font-bold text-lg text-white">Ti Khon</span>
            }
            name="taiKhoan"
            rules={[
              {
                required: true,
                message: "Please input your username!",
              },
            ]}
          >
            <Input disabled />
          </Form.Item>

          <Form.Item
            label={
              <span className="font-bold text-lg text-white">Mt khu</span>
            }
            name="matKhau"
            rules={[
              {
                required: true,
                message: "Vui lng nhp mt khu",
              },
              {
                pattern: new RegExp(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/),
                message:
                  "t nht 8 k t, c t nht 1 ch v 1 s v khng c k t c bit",
              },
            ]}
          >
            <Input.Password />
          </Form.Item>

          <Form.Item
            label={<span className="font-bold text-lg text-white">H Tn</span>}
            name="hoTen"
            rules={[
              {
                required: true,
                message: "Vui lng nhp h tn!",
                whitespace: true,
              },
              {
                pattern: new RegExp(/^([^0-9]*)$/),
                message: "Tn khng c cha s",
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label={<span className="font-bold text-lg text-white">Email</span>}
            name="email"
            rules={[
              {
                type: "email",
                message: "Email khng hp l!",
              },
              {
                required: true,
                message: "Vui lng in Email!",
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label={
              <span className="font-bold text-lg text-white">in Thoi</span>
            }
            name="soDT"
            rules={[
              {
                required: true,
                message: "Vui lng in s in thoi",
              },
              {
                min: 8,
                message: "t nht 8 ch s",
              },
              {
                pattern: new RegExp(/^[0-9]+$/),
                message: "nh dng khng hp l",
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            label={<span className="font-bold text-lg text-white">Type</span>}
            name="maLoaiNguoiDung"
            rules={[
              {
                required: true,
                message: "M ngi dng khng hp l!",
              },
            ]}
          >
            <Input disabled />
          </Form.Item>
          <Form.Item />
          <Form.Item
            wrapperCol={{
              offset: 8,
              span: 16,
            }}
          >
            <button
              className="bg-red-700 hover:bg-red-600 uppercase tracking-widest text-lg items-end text-white py-2 px-3 rounded"
              htmlType="submit"
            >
              Update
            </button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
}
