import { quanLyDatVeService } from "../../services/QuanLyDatVeService";
import {
  CHUYEN_TAB,
  DAT_VE,
  DAT_VE_HOAN_TAT,
  SET_CHI_TIET_PHONG_VE,
} from "./types/QuanLyDatVeTypes";
import { displayLoadingAction, hideLoadingAction } from "./LoadingAction";
import { connection } from "../../index";
import { ThongTinDatVe } from "../../_core/models/ThongTinDatVe";

export const layChiTietPhongVeAction = (maLichChieu) => {
  return async (dispatch) => {
    try {
      dispatch(displayLoadingAction);
      const { data, status } = await quanLyDatVeService.layChiTietPhongVe(
        maLichChieu
      );
      if (status === 200) {
        dispatch({
          type: SET_CHI_TIET_PHONG_VE,
          chiTietPhongVe: data.content,
        });
      } else {
        console.log("c li g goy");
      }
      dispatch(hideLoadingAction);
    } catch (err) {
      dispatch(hideLoadingAction);

      console.log("err: ", err);
    }
  };
};
export const datVeAction = (thongTinDatVe = new ThongTinDatVe()) => {
  console.log("thongTinDatVe: ", thongTinDatVe);
  return async (dispatch, getState) => {
    try {
      dispatch(displayLoadingAction);
      const { data, status } = await quanLyDatVeService.datVe(thongTinDatVe);
      console.log("status: ", status);
      console.log("data: ", data);
      // ! t await v dispatch l hm bt ng b ==> t await  trc th n ch dng 40 chy xong mi chy tip dng 41
      await dispatch(layChiTietPhongVeAction(thongTinDatVe.maLichChieu));
      await dispatch({
        type: DAT_VE_HOAN_TAT,
      });
      // ! khi t v thnh cng th gi ln socket cho server bit  cc browser khc c th cp nht giao din
      let userLogin = getState().QuanLyNguoiDungReducer.userLogin;
      console.log("userLogin: ", userLogin);
      connection.invoke(
        "datGheThanhCong",
        userLogin.taiKhoan,
        thongTinDatVe.maLichChieu
      );
      await dispatch({
        type: CHUYEN_TAB,
      });
      dispatch(hideLoadingAction);
    } catch (err) {
      console.log("err: ", err);
      dispatch(hideLoadingAction);
    }
  };
};
export const datGheAction = (ghe, maLichChieu) => {
  return async (dispatch, getState) => {
    // ! getState gip kt ni vi rootReducer
    try {
      await dispatch({
        type: DAT_VE,
        gheDuocChon: ghe,
      });
      //Call api v backend
      let danhSachGheDangDat = getState().QuanLyDatVeReducer.danhSachGheDangDat;
      let taiKhoan = getState().QuanLyNguoiDungReducer.userLogin.taiKhoan;
      console.log("danhSachGheDangDat:", danhSachGheDangDat);
      console.log("taiKhoan:", taiKhoan);
      console.log("maLichChieu:", maLichChieu);
      //Bin mng thnh chui
      danhSachGheDangDat = JSON.stringify(danhSachGheDangDat);

      //Call api signalR
      connection.invoke("datGhe", taiKhoan, danhSachGheDangDat, maLichChieu);
      // dispatch(hideLoadingAction);
    } catch (err) {
      dispatch(hideLoadingAction);
    }
  };
};
