import { Button, Form, Input, message, Select } from 'antd';
import { Option } from 'antd/lib/mentions';
import { useFormik } from 'formik';
import React from 'react'
import { useDispatch } from 'react-redux';
import * as yup from "yup";
import { addUserAction } from '../../../../redux/actions/QuanLyNguoiDungAction';
import { GROUPID } from '../../../../util/settings/config';
const { Item } = Form;
export default function AddUser(props) {
    const dispatch = useDispatch();
    const { history } = props;
    const formik = useFormik({
        initialValues: {
            taiKhoan: "",
            matKhau: "",
            email: "",
            soDt: "",
            maNhom: "GP01",
            maLoaiNguoiDung: "KhachHang",
            hoTen: ""
        },
        validationSchema: yup.object().shape({
            taiKhoan: yup
                .string()
                .min(4, "ti khon khng chp nhn ch in hoa, min 4, max 10, khng k t c bit")
                .max(20, "ti khon khng chp nhn ch in hoa, min 4, max 10, khng k t c bit")
                .matches(/^[a-z][a-z0-9_.]*$/, "ti khon khng chp nhn ch in hoa, min 4, max 10, khng k t c bit ")
                .required("Vui lng nhp ti khon"),
            matKhau: yup
                .string()
                .matches(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/, "t nht 8 k t, c t nht 1 ch v 1 s")
                .required("Vui lng nhp thng tin"),
            email: yup.string().email("Sai nh dng email").required("Vui lng nhp thng tin"),
            soDt: yup
                .string()
                .matches(/(03|05|07|08|09|01[2|6|8|9])+([0-9]{8})\b/, "Vui lng nhp ng s in thoi")
                .required("Vui lng nhp thng tin"),
            hoTen: yup
                .string()
                .matches(/^([^0-9]*)$/, "Tn khng c cha s")
                .required("Vui lng nhp thng tin")
        }),
        onSubmit: values => {
            handleAddUser(values);
        }
    });
    const handleChange = value => {
        formik.setFieldValue("maLoaiNguoiDung", value);
    };
    const handleAddUser = async (values) => {
        // console.log("vaalues: ", values);
        const result = await dispatch(addUserAction(values))
        if (!result) return;
        // console.log('result: ', result);
        history.push("/admin/users")
        // try {
        //     // ! thm thnh cng th thng bo sau  quay v trang qun l user
        //     // const result = await QuanLyNguoiDungService.ThemNguoiDung(values);
        //     // message.success("Bn thm thnh cng ri n", 1);
        //     // setTimeout(() => {
        //     //     // navigate("/admin/users");
        //     //     // dispatch(hide_loading());
        //     // }, 1000);
        // } catch (error) {
        //     message.error(error.response.data.content);
        //     // dispatch(hide_loading());
        // }
    };


    return (
        <div className="container">
            <Form
                onSubmitCapture={() => {
                    formik.handleSubmit();
                }}
                labelCol={{
                    span: 4
                }}
                wrapperCol={{
                    span: 12
                }}
                initialValues={{
                    remember: true
                }}
                autoComplete="on">
                <Item className="text-white" label="Ti khon">
                    <Input name="taiKhoan" onChange={formik.handleChange} value={formik.values.taiKhoan} />
                    {formik.errors.taiKhoan && formik.touched.taiKhoan && (
                        <p className="text-red-500">{formik.errors.taiKhoan}</p>
                    )}
                </Item>
                <Item className="text-white" label="H tn">
                    <Input name="hoTen" onChange={formik.handleChange} value={formik.values.hoTen} />
                    {formik.errors.hoTen && formik.touched.hoTen && <p className="text-red-500">{formik.errors.hoTen}</p>}
                </Item>
                <Item label="Mt khu">
                    <Input.Password
                        autoComplete="on"
                        name="matKhau"
                        onChange={formik.handleChange}
                        value={formik.values.matKhau}
                    />
                    {formik.errors.matKhau && formik.touched.matKhau && <p className="text-red-500">{formik.errors.matKhau}</p>}
                </Item>
                <Item className="text-white" label="Email">
                    <Input value={formik.values.email} name="email" onChange={formik.handleChange} />
                    {formik.errors.email && formik.touched.email && <p className="text-red-500">{formik.errors.email}</p>}
                </Item>
                <Item className="text-white" label="S in thoi">
                    <Input value={formik.values.soDt} name="soDt" onChange={formik.handleChange} />
                    {formik.errors.soDt && formik.touched.soDt && <p className="text-red-500">{formik.errors.soDt}</p>}
                </Item>
                <Item className="text-white" label="Loi ngi dng">
                    <Select
                        defaultValue="KhachHang"
                        style={{
                            width: "100%"
                        }}
                        onChange={handleChange}>
                        <Option value="KhachHang">Khch hng</Option>
                        <Option value="QuanTri">Qun l</Option>
                    </Select>
                </Item>

                <Item
                    wrapperCol={{
                        offset: 8,
                        span: 16
                    }}>
                    <Button
                        // onClick={() => {
                        // }}
                        htmlType="submit"
                        type="primary">
                        Submit
                    </Button>
                </Item>
            </Form>
        </div>
    )
}
