import React, { Fragment, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  datGheAction,
  datVeAction,
  layChiTietPhongVeAction,
} from "../../redux/actions/QuanLyDatVeAction";
import {
  CloseOutlined,
  UserOutlined,
  CheckOutlined,
  SmileOutlined,
  HomeOutlined,
} from "@ant-design/icons";
import style from "./Checkout.module.css";
import _ from "lodash";
import "./Checkout.css";
import {
  CHUYEN_TAB_ACTIVE,
  DAT_GHE,
  DAT_VE,
} from "../../redux/actions/types/QuanLyDatVeTypes";
import { ThongTinDatVe } from "../../_core/models/ThongTinDatVe";
import { TOKEN, USER_LOGIN } from "../../util/settings/config";
import { Tabs } from "antd";
import { quanLyNguoiDungService } from "../../services/QuanLyNguoiDungService";
import { layThongTinNguoiDungAction } from "../../redux/actions/QuanLyNguoiDungAction";
import moment from "moment/moment";
import { connection } from "../../index";
import { history } from "../../App";
import { NavLink } from "react-router-dom";
const { TabPane } = Tabs;
console.log("TabPane: ", TabPane);
function Menu(props) {
  const { userLogin } = useSelector((state) => state.QuanLyNguoiDungReducer);
  const { chiTietPhongVe, danhSachGheDangDat, danhSachGheKhachDat } =
    useSelector((state) => state.QuanLyDatVeReducer);
  const { thongTinPhim } = chiTietPhongVe;
  const { danhSachGhe } = chiTietPhongVe;
  const { id } = props.match.params;
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(layChiTietPhongVeAction(id));
    const action = layChiTietPhongVeAction(id);
    // !logic:  nu c 1 browser khc t v thnh cng th bn mnh load li giao din
    connection.on("datVeThanhCong", () => {
      dispatch(action);
    });
    //! logic: khi component render xong th load tt c gh ca ngi khc ang t v browser
    connection.invoke("loadDanhSachGhe", id);

    // ! Load nhng gh ang c ngi khc t t server v = socket
    connection.on("loadDanhSachGheDaDat", (dsGheKhachDat) => {
      // !logic: sau khi ly v th loi tn mnh ra khi danh sch, sau  to thnh 1 mng gh ca khch khc, test unique ID gh = uniqBy sau  gi ln reducer
      // ! Loi mnh ra khi danh sch
      dsGheKhachDat = dsGheKhachDat.filter(
        (item) => item.taiKhoan !== userLogin.taiKhoan
      );
      // ! sau  to thnh 1 mng gh ca khch khc

      let arrGheKhachDat = dsGheKhachDat.reduce((result, item, index) => {
        let arrGhe = JSON.parse(item.danhSachGhe);
        return [...result, ...arrGhe];
      }, []);

      // ! unique ID
      arrGheKhachDat = _.uniqBy(arrGheKhachDat, "maGhe");

      // ! gi ln reducer
      dispatch({
        type: DAT_GHE,
        arrGheKhachDat,
      });

      // ! ch ny ci logic l nu ngi dng f5 web hoc chuyn tab th xa i nhng gh ang c h click ( )
      //! f5 web
      window.addEventListener("beforeunload", clearGhe);
      // !chuyn tab--> component will unmount --> return trong useEffect

      return () => {
        clearGhe();
        window.removeEventListener("beforeunload", clearGhe);
      };
    });
  }, []);
  const clearGhe = function (event) {
    connection.invoke("huyDat", userLogin.taiKhoan, id);
  };
  const renderSeats = () => {
    return danhSachGhe.map((ghe, index) => {
      // ! start x l css gh ang t
      let indexGheDangDat = danhSachGheDangDat.findIndex(
        (gheDD) => gheDD.maGhe === ghe.maGhe
      );
      let classGheDangDat = indexGheDangDat !== -1 ? "gheDangDat" : "";
      // ! end x l css gh ang t

      // ! start css gh c mnh t
      let classGheDaDuocDat =
        ghe.taiKhoanNguoiDat === userLogin.taiKhoan ? "gheDaDuocDat" : "";
      // ! end css gh c mnh t

      //todo: start css gh ngi khc ang t
      let indexGheKhachDat = danhSachGheKhachDat.findIndex(
        (gheKD) => gheKD.maGhe === ghe.maGhe
      );
      let classGheKhachDat = indexGheKhachDat !== -1 ? "gheKhachDat" : "";
      //todo: end css gh ngi khc ang t
      let classGheVip = ghe.loaiGhe === "Vip" ? "gheVip" : "";
      let classGheDaDat = ghe.daDat ? "gheDaDat" : "";
      return (
        <Fragment key={index}>
          <button
            onClick={() => {
              dispatch(datGheAction(ghe, id));
            }}
            disabled={ghe.daDat || classGheKhachDat !== ""}
            className={`ghe ${classGheVip} ${classGheDaDat} ${classGheDangDat}  ${classGheDaDuocDat} ${classGheKhachDat} text-center `}
          >
            {/* //! nu  t th so snh xem mnh t hay ngi khc t==> nu ngi khc t th xem coi l ang t hay  t */}
            {ghe.daDat ? (
              classGheDaDat !== "" ? (
                <UserOutlined
                  style={{ marginBottom: 7.5, fontWeight: "bold" }}
                />
              ) : (
                <CloseOutlined
                  style={{ marginBottom: 7.5, fontWeight: "bold" }}
                />
              )
            ) : classGheKhachDat !== "" ? (
              <SmileOutlined />
            ) : (
              ghe.stt
            )}
          </button>
          {/* 
                    //! sau mi 16 ci gh th s  xung hng
                     */}
          {(index + 1) % 16 === 0 ? <br /> : ""}
        </Fragment>
      );
    });
  };
  const handleDatVe = () => {
    const thongTinDatVe = new ThongTinDatVe();
    console.log("thongTinDatVe: ", thongTinDatVe);
    thongTinDatVe.maLichChieu = id;
    thongTinDatVe.danhSachVe = danhSachGheDangDat;
    dispatch(datVeAction(thongTinDatVe));
  };
  return (
    <div className=" h-screen ">
      <div className="grid grid-cols-12">
        <div className="col-span-9">
          <div className="flex flex-col items-center mt-5">
            <div
              className="bg-black"
              style={{
                width: "80%",
                height: 15,
              }}
            ></div>
            <div className={`${style.trapezoid} text-center`}>
              <h3 className="mt-3 text-black">Mn hnh</h3>
            </div>
            <div>{renderSeats()}</div>
          </div>
          <div className="mt-5 flex justify-center">
            <table className="divide-y divide-gray-200 w-2/3">
              <thead className="bg-gray-50 p-5">
                <tr>
                  <th>Gh cha t</th>
                  <th>Gh ang t</th>
                  <th>Gh vip t</th>
                  <th>Gh  t</th>
                  <th>Gh mnh t</th>
                  <th>Gh khch t</th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                <tr>
                  <td className="text-center">
                    <button className="ghe  ">
                      <CheckOutlined
                        style={{ marginBottom: 7.5, fontWeight: "bold" }}
                      />
                    </button>
                  </td>
                  <td className="text-center">
                    <button className="ghe gheDangDat  ">
                      <CheckOutlined
                        style={{ marginBottom: 7.5, fontWeight: "bold" }}
                      />
                    </button>
                  </td>
                  <td className="text-center">
                    <button className="ghe gheVip  ">
                      <CheckOutlined
                        style={{ marginBottom: 7.5, fontWeight: "bold" }}
                      />
                    </button>
                  </td>
                  <td className="text-center">
                    <button className="ghe gheDaDat  ">
                      <CheckOutlined
                        style={{ marginBottom: 7.5, fontWeight: "bold" }}
                      />
                    </button>
                  </td>
                  <td className="text-center">
                    <button className="ghe gheDaDuocDat  ">
                      <CheckOutlined
                        style={{ marginBottom: 7.5, fontWeight: "bold" }}
                      />
                    </button>
                  </td>
                  <td className="text-center">
                    <button className="ghe gheKhachDat gheDaDuocDat  ">
                      <CheckOutlined
                        style={{ marginBottom: 7.5, fontWeight: "bold" }}
                      />
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div className="col-span-3  h-screen">
          <div className="flex flex-col justify-between h-full pt-5">
            <div style={{ height: "92%" }}>
              <h3 className="text-green-400 text-center text-2xl">
                {" "}
                {danhSachGheDangDat
                  .reduce((tongTien, ghe, index) => {
                    return (tongTien += ghe.giaVe);
                  }, 0)
                  .toLocaleString()}{" "}
                
              </h3>
              <hr />
              <h3 className="text-xl">{thongTinPhim.tenPhim}</h3>
              <p>a im: {thongTinPhim.diaChi}</p>
              <p>
                Ngy chiu: {thongTinPhim.ngayChieu} {thongTinPhim.gioChieu}
              </p>
              <hr />
              <div className="flex flex-row my-5">
                <div className="w-4/5">
                  <span className="text-red-400 text-lg">Gh</span>
                  {_.sortBy(danhSachGheDangDat, ["stt"]).map((gheDD, index) => {
                    return (
                      <Fragment key={index}>
                        <span className="text-green-500 text-xl mx-2">
                          {gheDD.stt}
                        </span>
                      </Fragment>
                    );
                  })}
                </div>
                <div className="text-right col-span-1">
                  <span className="text-green-800 text-lg">
                    {danhSachGheDangDat
                      .reduce((tongTien, ghe, index) => {
                        return (tongTien += ghe.giaVe);
                      }, 0)
                      .toLocaleString()}
                  </span>
                </div>
              </div>
              <div className="my-5">
                <i>Email</i>
                <br />
                {userLogin.email}
              </div>
              <div className="my-5">
                <i>Phone</i>
                <br />
                {userLogin.soDT}
              </div>
            </div>
            <div style={{ height: "8%" }}>
              <button
                onClick={handleDatVe}
                className="bg-red-700 h-full text-white w-full text-center py-3 font-bold text-2xl cursor-pointer"
              >
                t v
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

function KetQuaDatVe() {
  const dispatch = useDispatch();
  const { thongTinNguoiDung } = useSelector(
    (state) => state.QuanLyNguoiDungReducer
  );
  console.log("thongTinNguoiDung: ", thongTinNguoiDung);
  // ! sau khi render xong th ly thng tin ngi dng v gn ln giao din, API ang li
  useEffect(() => {
    dispatch(layThongTinNguoiDungAction());
  }, []);
  const renderTicketItem = () => {
    return thongTinNguoiDung.thongTinDatVe
      ?.slice(0)
      .reverse()
      .map((ticket, index) => {
        const seats = _.first(ticket.danhSachGhe);
        let ticketInfo = thongTinNguoiDung.thongTinDatVe[index].danhSachGhe;
        let soGhe = ticketInfo.map(({ tenGhe }) => tenGhe);
        return (
          <Fragment key={index.toString()}>
            <div className="p-2 lg:w-1/3 md:w-1/2 w-full">
              <div className="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <img
                  alt="team"
                  className="max-w-32 max-h-32 bg-gray-100 object-cover object-center flex-shrink-0 mr-4"
                  src={ticket.hinhAnh}
                />
                <div className="flex-grow">
                  <h2 className="text-gray-900 title-font font-medium">
                    {ticket.tenPhim}
                  </h2>
                  <p className="text-gray-500">
                    Gi chiu: {moment(ticket.ngayDat).format("hh:mm A")} - Ngy
                    chiu {moment(ticket.ngayDat).format("DD-MM-YYYY")}.
                  </p>
                  <p>
                    a im: {seats.tenHeThongRap} - {seats.tenCumRap}
                  </p>
                  <p>S gh: {soGhe.join(", ")} </p>
                </div>
              </div>
            </div>
          </Fragment>
        );
      });
  };
  return (
    <div className="p-5">
      <section className="text-gray-600 body-font">
        <div className="container px-5 mx-auto">
          <div className="flex flex-col text-center w-full">
            <h3 className="sm:text-3xl text-2xl font-medium text-red-700 uppercase">
              Lch s t v
            </h3>
          </div>
          <div className="flex flex-wrap -m-2">{renderTicketItem()}</div>
        </div>
      </section>
    </div>
  );
}

