import React, { useEffect, useState } from "react";
import {
    Button,
    Cascader,
    DatePicker,
    Form,
    Input,
    InputNumber,
    Radio,
    Select,
    Switch,
    TreeSelect,
} from "antd";
import { useFormik } from "formik";
import moment from "moment/moment";
import { useDispatch } from "react-redux";
import {
    capNhatFilmUploadAction,
    quanLyPhimAction,
    themPhimUploadHinhAction,
} from "../../../../redux/actions/QuanLyPhimAction";
import { GROUPID } from "../../../../util/settings/config";
import { useSelector } from "react-redux";
export default function Edit(props) {
    const { id } = props.match.params;
    const [componentSize, setComponentSize] = useState("default");
    const [imgSrc, setImgSrc] = useState(null);
    const dispatch = useDispatch();
    // ! ly thng tin phim trn reducer v
    const { thongTinFilm } = useSelector((state) => state.QuanLyPhimReducer);
    console.log('thongTinFilm: ', thongTinFilm);
    const dateFormat = "DD/MM/YYYY";
    // !  ngha ca set hnh nh l null l v nu hnh nh = null th backend vn gi nguyn nh c
    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            maPhim: thongTinFilm.maPhim,
            tenPhim: thongTinFilm.tenPhim,
            trailer: thongTinFilm.trailer,
            moTa: thongTinFilm.moTa,
            ngayKhoiChieu: thongTinFilm.ngayKhoiChieu,
            dangChieu: thongTinFilm.dangChieu,
            sapChieu: thongTinFilm.sapChieu,
            hot: thongTinFilm.hot,
            danhGia: thongTinFilm.danhGia,
            hinhAnh: null,
        },
        onSubmit: (values) => {
            console.log('values: ', values);
            values.maNhom = GROUPID;
            // ! logic: cn gi d liu ny ln API nhng API nhn vo d liu dng formData  ( v c hnh nh) ==> phi convert d liu trn giao din sang d liu dng formData
            // !1. To i tng formData
            let formData = new FormData();
            for (let key in values) {
                if (key !== "hinhAnh") {
                    formData.append(key, values[key]); //! n nhn vo ts1: tn , ts2: gi tr==>  dng for in  ko repeat code
                } else {
                    //! nhng i vi d liu dng hnh nh th phi truyn 3ts
                    if (values.hinhAnh !== null) { //! ci bc ny cho n ko warning na
                        formData.append("File", values.hinhAnh, values.hinhAnh.name);
                    }
                }
            }
            // ! gi d liu v backend
            dispatch(capNhatFilmUploadAction(formData));
        },
    });

    // ! khi dc render xong th gi action  ly thng tin phim v  gn ln component
    useEffect(() => {
        dispatch(quanLyPhimAction(id));
    }, []);

    const handleChangeDatePicker = (value) => {
        let ngayKhoiChieu = moment(value).format("DD/MM/YYYY");
        formik.setFieldValue("ngayKhoiChieu", ngayKhoiChieu);
    };
    // ! 2 hm ny dng k thut closure function => mc ch l  ly key ng ( ko phi hashcode ) ==> ko cn phi g li nhiu
    const handleChangeSwitch = (name) => {
        // ! Switch ca antd tr v value  tham s nn ==> dng closer function nh th ny
        return (value) => {
            formik.setFieldValue(name, value);
        };
    };
    const handleChangeInputNumber = (name) => {
        return (value) => {
            formik.setFieldValue(name, value);
        };
    };
    // ! x l ly hnh nh  a vo formik
    const handleChangeFile = async (event) => {
        // !!!! logic: bi v set Filed value l hm bt ng b==> kim sot code = cch dng async await
        // ! ly file t event
        let file = event.target.files[0];
        if (
            //! gii hn==> nu d liu l nh
            file.type == "image/jpeg" ||
            file.type == "image/jpg" ||
            file.type == "image/gif" ||
            file.type == "image/png"
        ) {
            // ! em d liu file lu vo formik
            await formik.setFieldValue("hinhAnh", file);
            // ! c file  hin th nh ln giao din ch ko lin quan n gn d liu vo formik
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = (event) => {
                setImgSrc(event.target.result);
            };
        }
    };

    const onFormLayoutChange = ({ size }) => {
        setComponentSize(size);
    };

    return (
        // ! submit ca form antd l onsubmitcapture==> gn hm submit ca formik vo
        <Form
            onSubmitCapture={formik.handleSubmit}
            labelCol={{
                span: 4,
            }}
            wrapperCol={{
                span: 14,
            }}
            layout="horizontal"
            initialValues={{
                size: componentSize,
            }}
            onValuesChange={onFormLayoutChange}
            size={componentSize}
        >
            <Form.Item label="Form Size" name="size">
                <Radio.Group>
                    <Radio.Button value="small">Small</Radio.Button>
                    <Radio.Button value="default">Default</Radio.Button>
                    <Radio.Button value="large">Large</Radio.Button>
                </Radio.Group>
            </Form.Item>
            <Form.Item label="Tn phim">
                <Input
                    name="tenPhim"
                    value={formik.values.tenPhim}
                    onChange={formik.handleChange}
                />
                {/* tenPhim: thongTinFilm.tenPhim,
            trailer: "",
            moTa: "",
            ngayKhoiChieu: "",
            dangChieu: false,
            sapChieu: false,
            hot: false,
            danhGia: 0,
            hinhAnh: {} */}
            </Form.Item>
            <Form.Item label="Trailer">
                <Input
                    value={formik.values.trailer}
                    name="trailer"
                    onChange={formik.handleChange}
                />
            </Form.Item>
            <Form.Item label="M t">
                <Input
                    value={formik.values.moTa}
                    name="moTa"
                    onChange={formik.handleChange}
                />
            </Form.Item>
            <Form.Item label="Ngy khi chiu">
                <DatePicker
                    // formik.values.ngayKhoiChieu
                    defaultValue={moment(moment(formik.values.ngayKhoiChieu).format(dateFormat), dateFormat)}
                    // defaultValue={moment('2015/01/01', dateFormat)}
                    onChange={handleChangeDatePicker}
                    format={dateFormat}
                />
            </Form.Item>
            <Form.Item label="ang chiu" valuePropName="checked">
                <Switch
                    defaultChecked={formik.values.dangChieu}
                    onChange={handleChangeSwitch("dangChieu")}
                />
            </Form.Item>
            <Form.Item label="Sp chiu" valuePropName="checked">
                <Switch
                    defaultChecked={formik.values.sapChieu}
                    onChange={handleChangeSwitch("sapChieu")}
                />
            </Form.Item>
            <Form.Item label="Hot" valuePropName="checked">
                <Switch
                    defaultChecked={formik.values.checked}
                    onChange={handleChangeSwitch("hot")}
                />
            </Form.Item>

            <Form.Item label="S sao">
                <InputNumber
                    defaultValue={formik.values.danhGia}
                    min={1}
                    max={10}
                    onChange={handleChangeInputNumber("danhGia")}
                />
            </Form.Item>
            <Form.Item label="Hnh nh">
                <input type="file" onChange={handleChangeFile} accept="image/*" />
                <br />
                <img
                    src={imgSrc == null ? thongTinFilm.hinhAnh : imgSrc}
                    alt="..."
                    width={150}
                    height={150}
                />
            </Form.Item>
            <Form.Item label="Tc v">
                <button
                    type="submit"
                    className=" bg-green-400 text-white p-2 rounded-md"
                >
                    Cp nht
                </button>
            </Form.Item>
        </Form>
    );
}
